html {
  height: 100vh;
}
body {
  color: white;
  background-image: linear-gradient(145deg, #8a2387, #e94057, #f27121);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  background-size: cover;
  background-repeat: no-repeat;
  height: 100%;
  display: block;
  font-family: sans-serif;
  font-size: 20px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 15px;
}
@media (max-width: 1240px) {
  body {
    font-size: calc((100vw - 320px)/920 * 4 + 16px);
  }
}
@media (max-width: 600px) {
  body {
    font-size: 16px;
    font-size: calc((100vw - 320px)/920 * 4 + 16px);
  }
}
@media (max-width: 600px) {
  body {
    padding: 2%;
  }
}
main {
  margin: 0 auto;
  max-width: 500px;
}
form {
  background: rgba(0, 0, 0, 0.2);
  /* fallback for old browsers */
  border-radius: 5px;
  border: 1px solid #343434;
  padding: 0.4em;
  box-shadow: 0px 10px 40px 5px rgba(0, 0, 0, 0.2);
}
.input-text {
  margin-bottom: 5px;
  position: relative;
  display: flex;
  align-items: center;
}
.input-text span {
  position: absolute;
  left: .8em;
  top: .7em;
  color: #d7d7d7;
}
.input {
  color: #d5d5d5;
  width: 100%;
  outline: none;
  border-radius: 5px;
  background: rgba(0, 0, 0, 0);
  padding: 0.2em;
  margin-top: 0.7em;
  border: none;
  border-bottom: 2px solid rgba(0, 0, 0, 0.15);
  border-left: 0.3em solid rgba(0, 0, 0, 0);
  border-right: 0.3em solid rgba(0, 0, 0, 0);
  transition: all 0.2s ease-out;
}
.input:hover {
  background-size: contain;
  background-repeat: no-repeat;
  box-shadow: inset 0 -1em 1em -1em rgba(0, 0, 0, 0.3);
}
.input:focus {
  outline: none;
  border-left: 0.3em solid rgba(0, 0, 0, 0.5);
  border-right: 0.3em solid rgba(0, 0, 0, 0.5);
  transition: all 0.2s ease-out;
  box-shadow: inset 0 -1em 1em -1em rgba(0, 0, 0, 0);
}
.input.not-empty ~ span,
.input:focus ~ span {
  transition: all 0.2s ease-out;
  top: -0.5em;
  color: #a9a9a9;
}
